      h4 Transporte areo          
    .row.mb-5
      .col-lg-5
        img(src='@/assets/curso/img19.png', alt='Texto que describa la imagen').mb-2

      .col-lg-7
        TabsC.color-acento-contenido.mb-5
          .py-3.py-md-4(titulo="Caractersticas")
            img(src='@/assets/curso/img23.jpg', alt='Texto que describa la imagen').mb-3
            p.mb-3
              b Rapidez: 
              | es el medio ms veloz que existe, tiene un proceso logstico ideal.

            p.mb-3
              b Seguridad: 
              | segn los ndices es uno de los medios de transporte ms seguros, al igual que el ferroviario.
            p.mb-3
              b Flexibilidad: 
              | existe una diversidad de aviones adaptados al tipo de transporte que realizan cargas mayores o igual a 100 toneladas.
            p.mb-3
              b Control de seguimiento: 
              |  realizan un seguimiento profundo y detallado del lugar exacto en el que se encuentra la mercanca a cada momento.
          .py-3.py-md-4(titulo="Ventajas")
            img(src='@/assets/curso/img24.jpg', alt='Texto que describa la imagen').mb-3
            p.mb-3
                b Velocidad:  
                | vehculo de alta velocidad.
            p.mb-3
                b  Competitividad: 
                | reduce gastos.
            p.mb-3
                b  Documentacin: 
                | simple y normalizada totalmente.
            p.mb-3
                b Cobertura:  
                | tiene gran cobertura en diferentes pases en el momento de transportar la carga.
          .py-3.py-md-4(titulo="Desventajas")
            img(src='@/assets/curso/img25.jpg', alt='Texto que describa la imagen').mb-3
            p.mb-3
                b Capacidad:   
                | el avin no est en capacidad de competir con los dems medios de transporte por razn de peso y volumen.

            p.mb-3
                b  Cargas a granel:  
                |  no es el medio de transporte adecuado para carga o transporte de lquidos, minerales, petrleo, entre otros.
            p.mb-3
                b  Productos de bajo valor unitario: 
                | las materias primas no pueden absorber mayor costo del flete. 
            p.mb-3
                b  Artculos peligrosos: 
                | el IATA (Asociacin Internacional de Transporte Areo) y el OACI (Organizacin de Aviacin Civil Internacional) se encarga de restringir todos aquellos artculos peligrosos en los aviones por seguridad.
    .titulo_recuadro.color__before--primario.mb-5
      h4 Transporte ferroviario          
    .row.mb-5
      .col-lg-5
