            p.mb-3 Permite la operacin de vehculos en trenes.
              br
              br
              |La gua externa tiene costos muy altos de inventario.
              br
              br
              |La tecnologa usada en este transporte es sencilla y permite realizar ajustes o cambios de direccin rpidos y fciles.
              br
              br
              |Posee variedad de rendimiento sobre todo cuando se trata de la aceleracin.
              br
              br
              |El manejo de esta tecnologa es ms bajo en comparacin con los de rodada automtica.
                
          .py-3.py-md-4(titulo="Ventajas")
            img(src='@/assets/curso/img27.jpg', alt='Texto que describa la imagen').mb-3
            p.mb-3
                b Capacidad:   
                | tiene grandes ventajas gracias a su mayor capacidad en relacin con la del transporte carretero o areo, cuando se trata de volmenes de gran carga.
            p.mb-3
                b  Flexibilidad combinada:  
                | se da dentro del sistema ferro acutico cuando los vagones de tren se transportan en buques porta trenes.
                br
                br
                |Cuentan con vas y rieles que permiten alcanzar una buena velocidad. 
            p.mb-3
                b Documentacin:     
                | es similar a la que se necesita para el transporte por carretera.
          .py-3.py-md-4(titulo="Desventajas")
            img(src='@/assets/curso/img28.jpg', alt='Texto que describa la imagen').mb-3
            p.mb-3 Su flexibilidad es poca, ya que se deriva de las caractersticas de la red y las diferencias en el ancho de las vas.
              br
              br
              |Necesita otro medio de transporte para trasbordar.
              br
              br
              |Est expuesto a saqueos.

