            p.mb-3
                b  Capacidad:   
                | supera el milln de toneladas de peso muerto.

            p.mb-3
                b Flexibilidad: 
                |  viene dada por la alternativa del empleo de buques de mediano y pequeo tamao hasta busques de gran envergadura.

            p.mb-3
                b Competencia: 
                |  la operacin casi siempre se realiza bajo la norma y las leyes del mercado de fletes.
            p.mb-3
                b Versatilidad: 
                | cuenta con varias clases de buques, permitiendo la acomodacin fcil y eficaz de la mercanca.

          .py-3.py-md-4(titulo="Ventajas")
            img(src='@/assets/curso/img30.jpg', alt='Texto que describa la imagen').mb-3
