              br
              br
              |Por lo general necesita de caminos pavimentados y acondicionados tales como carreteras, tneles y puentes, permitiendo sortear obstculos.
              br
              br
              |Es un mtodo predilecto de transporte de bienes y pasajeros en el mundo.
            
          .py-3.py-md-4(titulo="Ventajas")
            img(src='@/assets/curso/img33.jpg', alt='Texto que describa la imagen').mb-3
            p.mb-3 
              b Versatilidad: 
              | permite un acceso gil a las centrales, tanto para recoger como para entregar la mercanca.
              br
              br
              |Cuenta con diferentes tipos de vehculos.
              br
              br
              |Provee una amplia cobertura.
              br
              br
              |El conductor realiza un acompaamiento constante, permitindole realizar una supervisin de principio a fin y reduciendo daos o saqueos.
              br
              br
              |El transporte carretero tiene una baja frecuencia de embalaje, en algunos casos no es necesario.
              br
              br
              b Documentacin:  
              | es simple cuando se trata de formularios normalizados.
          .py-3.py-md-4(titulo="Desventajas")
            img(src='@/assets/curso/img34.jpg', alt='Texto que describa la imagen').mb-3
            p.mb-3 
